/**
 * 
 */
export default class Tips {
  constructor() {
    this.isLoading = false;
  }
  /**
   * 
   */

  static success(title, duration = 1000) {
    setTimeout(() => {
      uni.showToast({
        title: title,
        icon: "success",
        mask: true,
        duration: duration
      });
    }, 300);
    if (duration > 0) {
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve();
        }, duration);
      });
    }
  }

  /**
   * 
   */
  static confirm(text,showCancel, payload = {}, title = "") {
    return new Promise((resolve, reject) => {
      uni.showModal({
        title: title,
        content: text,
        showCancel: showCancel,
        success: res => {
          if (res.confirm) {
            resolve(payload);
          } else if (res.cancel) {
            reject(payload);
          }
        },
        fail: res => {
          reject(payload);
        }
      });
    });
  }

  static toast(title, onHide, icon = "none") {
    setTimeout(() => {
      uni.showToast({
        title: title,
        icon: icon,
        mask: true,
        duration:1000
      });
    }, 300);

    // 
    if (onHide) {
      setTimeout(() => {
        onHide();
      }, 500);
    }
  }

  /**
   * 
   */
  static alert(title) {
    // uni.showToast({
    //   title: title,
    //   image: "../../static/alert.png",
    //   mask: true,
    //   duration: 1500
    // });
  }

  /**
   * 
   */

  static error(title, onHide) {
    uni.showToast({
      title: title,
      image: "../../static/error.png",
      mask: true,
      duration: 1500
    });
    // 
    if (onHide) {
      setTimeout(() => {
        onHide();
      }, 500);
    }
  }

  /**
   * 
   */
  static loading(title = "") {
    if (Tips.isLoading) {
      return;
    }
    Tips.isLoading = true;
    uni.showLoading({
      title: title,
      mask: true
    });
  }

  /**
   * 
   */
  static loaded() {
    if (Tips.isLoading) {
      Tips.isLoading = false;
      uni.hideLoading();
    }
  }
}

/**
 * 
 */
Tips.isLoading = false;
