import Request from '@/common/luch-request/index.js'
import { ACCESS_TOKEN } from '@/common/util/constants.js'
import configService from './config.service.js'
import tip from '@/common/util/tip.js';
import store from '@/store/index.js';

let apiUrl = configService.apiUrl;
let fileUrl = configService.fileUrl;
const getTokenStorage = () => {
  let token = ''
  try {


    token = uni.getStorageSync(ACCESS_TOKEN)
    // console.log("token",token)
    // token = '10003'
    // window.sessionStorage.setItem('token', token)
  } catch (e) {
    //TODO handle the exception
    console.log("getTokenStorage", token)
    console.log("getTokenStorage", e.message)
  }
  return token
}



const http = new Request()
http.setConfig((config) => { /*  */
  config.baseUrl = apiUrl /*  */
  config.fileUrl = fileUrl
  config.header = {
    ...config.header
  }
  return config
})

/**
 * true (resolve)(reject)
 * @param { Number } statusCode - statusCode
 * @return { Boolean } true, resolve,  reject
 */
// 
http.validateStatus = (statusCode) => {
  return statusCode === 200
}

http.interceptor.request((config, cancel) => { /*  */
  // tip.alert(config.baseUrl)
  // loading
  uni.showLoading({
    title: "",
    mask: true, // 
  });
  config.header = {
    ...config.header,
    'X-Access-Token': getTokenStorage()
  }
  /*
  if (!token) { // tokencancel catch() 
    cancel('token ') // catch((err) => {}) err.errMsg === 'token '
  }
  */
  return config
})

// 
http.interceptor.response(async (response) => { /*  */
  uni.hideLoading() // loading
  return response
}, (response) => {
  // 
  console.log("", response);
  if (response) {
    let data = response.data
    const token = uni.getStorageSync(ACCESS_TOKEN)
    console.log("------------", token)
    console.log("------------", data)
    console.log("------------", data.status)
    switch (response.statusCode) {
      case 403:
        tip.error('Access denied');
        break
      case 503:
        break
      case 500:
        store.dispatch('Logout').then(() => {
          // clearTimeout(timeout)
          // window.location.reload()
          // tip.alert('')
        })
        break
      case 404:
        break
      case 504:
        break
      case 401:
        if (token) {
          /* store.dispatch('Logout').then(() => {
             setTimeout(() => {
               window.location.reload()
             }, 1500)
           }) */
        }
        break
      default:
        tip.error({
          duration: 0,
          forbidClick: true,
          message: data.message
        });
        break
    }
  }
  uni.hideLoading() // loading

  return response
})

export {
  http
}
