//APP

export default function appUpdate() {
	let _this=this;
	console.log(plus.runtime.version)
	uni.request({
		url: 'http://120.195.219.132:48086/81/update/update.json', //
		data: {
			appid: plus.runtime.appid,
			version: plus.runtime.version,
			imei: plus.device.imei
		},
		success: (res) => {
			plus.runtime.getProperty(plus.runtime.appid, function(wgtinfo) {
				let client_version = wgtinfo.version
				var flag_update = client_version.split(".").splice(0, 2).join(".") != res.data.version.split(".").splice(0, 2)
					.join(".");
				var flag_hot = (Number(client_version.split(".")[2]) < Number(res.data.version.split(".")[2])) & !flag_update
				console.log(res.data.version)
				console.log(client_version)
				console.log(flag_update)
				console.log(flag_hot)
				if (flag_update) {
					// 
					uni.showModal({
						title: '',
						content: "",
						success: (showResult) => {
							if (showResult.confirm) {
								_this.show=true;
								plus.nativeUI.toast("!");
								console.log(res.data.url, )
								var dtask = plus.downloader.createDownload(res.data.url, {
									method: 'GET',
									filename: '_doc/update/'
								}, function(d, status) {
									if (status == 200) {
										var path = d.filename; //apk
										plus.runtime.install(path); // apk
									} else {
										plus.nativeUI.alert(':' + status);
									}
								});
								dtask.start();
								// // 
								// let timer = setInterval(() => {
								// 	let percent = (dtask.downloadedSize / this.fileSize).toFixed(2) // fileSize
								// 	_this.percentVal = Math.floor(percent * 100) // 
								// 	if (percent >= 1) { // 
								// 		clearInterval(timer)
								// 	}
								// }, 18)
							}
						}
					})
				} else if (flag_hot) {
					uni.downloadFile({
						url: res.data.wgtUrl,
						success: (downloadResult) => {
							console.log(downloadResult.tempFilePath)
							if (downloadResult.statusCode === 200) {
								plus.nativeUI.toast(`!${res.data.versionCode}`);
								plus.runtime.install(downloadResult.tempFilePath, {
									force: false
								}, function() {
									plus.nativeUI.toast("");
									plus.runtime.restart();
								}, function(e) {
									console.log(e)
									plus.nativeUI.toast(`:${e.message}`);
								});
							}
						}
					});
				}

			});




		}
	})
}
