
import Vue from 'vue'
import Vuex from 'vuex'

import api from "@/api/api"
import configService from '@/common/service/config.service.js';
import MinCache from '@/common/util/MinCache.js'
import {
	http
} from '@/common/service/service.js'
import {
	ACCESS_TOKEN,
	USER_NAME,
	USER_INFO,
	USER_ID
} from "@/common/util/constants"

Vue.use(Vuex)
const store = new Vuex.Store({








				state: {
					messageNum: 0,
					hasLogin: false,
					isUniverifyLogin: false,
					loginProvider: "",
					openid: null,
					testvuex: false,
					colorIndex: 0,
					colorList: ['#FF0000', '#00FF00', '#0000FF'],
					noMatchLeftWindow: true,
					active: 'componentPage',
					leftWinActive: '/pages/component/view/view',
					activeOpen: '',
					menu: [],
					univerifyErrorMsg: '',
					token: '',
					userid: '',
					username: '',
					realname: '',
					userRole: null,
					avatar: '',
					phone: '',
					company: '',
					user: {
						username: uni.getStorageSync(USER_INFO).username,
						id: uni.getStorageSync(USER_INFO).id,
						realname: uni.getStorageSync(USER_INFO).realname,
					}
				},
				mutations: {
					setUserRole(state, role) {
						state.userRole = role;
					},
					submitMessageNum: (state, value) => {
						state.messageNum = value
					},
					SET_TOKEN: (state, token) => {
						state.token = token
					},
					SET_NAME: (state, {
						username,
						realname,
						welcome
					}) => {
						state.username = username
						state.realname = realname
						state.welcome = welcome
					},
					SET_AVATAR: (state, avatar) => {
						state.avatar = avatar
					},
					SET_PHONE: (state, phone) => {
						state.phone = phone
					},
					login(state, provider) {
						state.hasLogin = true;
						state.loginProvider = provider;
					},
					logout(state) {
						state.hasLogin = false
						state.openid = null
					},
					setOpenid(state, openid) {
						state.openid = openid
					},
					setTestTrue(state) {
						state.testvuex = true
					},
					setTestFalse(state) {
						state.testvuex = false
					},
					setColorIndex(state, index) {
						state.colorIndex = index
					},
					setMatchLeftWindow(state, matchLeftWindow) {
						state.noMatchLeftWindow = !matchLeftWindow
					},
					setActive(state, tabPage) {
						state.active = tabPage
					},
					setLeftWinActive(state, leftWinActive) {
						state.leftWinActive = leftWinActive
					},
					setActiveOpen(state, activeOpen) {
						state.activeOpen = activeOpen
					},
					setMenu(state, menu) {
						state.menu = menu
					},
					setUniverifyLogin(state, payload) {
						typeof payload !== 'boolean' ? payload = !!payload : '';
						state.isUniverifyLogin = payload;
					},
					setUniverifyErrorMsg(state, payload = '') {
						state.univerifyErrorMsg = payload
					}
				},
				getters: {
					currentColor(state) {
						return state.colorList[state.colorIndex]
					}
				},
				state: {
					currentColor(state) {
						return state.colorList[state.colorIndex]
					}
				},
				actions: {
					fetchUserRole({
						commit,
						getters
					}, value) {
						http.post(`/sys/user/selectUserRoleByUserId`, {
							id: value
						}).then(res => {
							const roleList = res.data.result.map(item => item.roleCode)
							commit('setUserRole',roleList)
							// if (roleList.includes('admin')) {
							// 	commit('setUserRole', 1);
							// } else {
							// 	commit('setUserRole', 0);
							// }
						})
					},
					changeMessageNum(context, value) {
						context.commit('submitMessageNum', value)
					},
					// 
					mLogin({
						commit
					}, userInfo) {
						return new Promise((resolve, reject) => {
							api.login(userInfo).then(response => {
								if (response.data.code == 200) {
									const result = response.data.result
									const userInfo = result.userInfo
									uni.setStorageSync(ACCESS_TOKEN, result.token);
									uni.setStorageSync(USER_INFO, userInfo);
									commit('SET_TOKEN', result.token)
									commit('SET_PHONE', userInfo.phone)
									commit('SET_AVATAR', userInfo.avatar)
									console.log("aaaaa", userInfo.phone)
									commit('SET_NAME', {
										username: userInfo.username,
										realname: userInfo.realname
									})
									resolve(response)
								} else {
									resolve(response)
								}
							}).catch(error => {
								console.log("catch===>response", response)
								reject(error)
							})
						})
					},
					h5Login(params) {
						console.log("h5Login", params)
						return new Promise((resolve, reject) => {
							api.h5login(params).then(response => {
								if (response.data.code == 200) {
									const result = response.data.result
									const userInfo = result.userInfo
									uni.setStorageSync(ACCESS_TOKEN, result.token);
									uni.setStorageSync(USER_INFO, userInfo);
									uni.setStorageSync(USER_ID, res.data.userId);
									commit('SET_TOKEN', result.token)
									commit('SET_PHONE', userInfo.phone)
									commit('SET_AVATAR', userInfo.avatar)
									commit('SET_NAME', {
										username: userInfo.username,
										realname: userInfo.realname
									})
									resolve(response)
								} else {
									resolve(response)
								}
							}).catch(error => {
								console.log("catch===>response", response)
								reject(error)
							})
						})
					},

					qyweixinLogin({ commit }, userInfo) {
						console.log("qyweixinLogin", userInfo)
						return new Promise((resolve, reject) => {
							api.qyweixinLogin(userInfo).then(response => {
								if (response.data.code == 200) {
									const result = response.data.result
									const userInfo = result.userInfo
									uni.setStorageSync(ACCESS_TOKEN, result.token);
									uni.setStorageSync(USER_INFO, userInfo);
									commit('SET_TOKEN', result.token)
									commit('SET_PHONE', userInfo.phone)
									commit('SET_AVATAR', userInfo.avatar)
									commit('SET_NAME', {
										username: userInfo.username,
										realname: userInfo.realname
									})
									resolve(response)
								} else {
									resolve(response)
								}
							}).catch(error => {
								console.log("catch===>response", response)
								reject(error)
							})
						})
					},
					//
					PhoneLogin({ commit }, userInfo) {
						return new Promise((resolve, reject) => {
							api.phoneNoLogin(userInfo).then(response => {
								if (response.code == '200') {
									const result = response.result
									const userInfo = result.userInfo
									commit('SET_TOKEN', result.token)
									commit('SET_PHONE', userInfo.phone)
									commit('SET_NAME', {
										username: userInfo.username,
										realname: userInfo.realname
									})
									commit('SET_AVATAR', userInfo.avatar)
									resolve(response)
								} else {
									reject(response)
								}
							}).catch(error => {
								reject(error)
							})
						})
					},
					// 
					Logout({commit,state}) {
						return new Promise((resolve) => {
							let logoutToken = state.token;
							commit('SET_TOKEN', '')
							uni.removeStorageSync(ACCESS_TOKEN)
							api.logout(logoutToken).then(() => {
								uni.clearStorageSync()
								const pages = getCurrentPages();
								// 
								const currentPage = pages[pages.length - 1];
								const currentRoute = currentPage.route;
								const queryParams = currentPage.options;
								uni.showModal({
									showCancel:false,
									confirmText:"Confirm",
									title:"Tip",
									content: 'Token invalid, please log in again!',
									success: function (res) {
											if (res.confirm) {
												uni.reLaunch({
													url:'/pages/login/login'
												})
											} 
										}
								})
								
								resolve()
							}).catch(() => {
								resolve()
							})
						})
					},

					// lazy loading openid
					getUserOpenId: async function({
						commit,
						state
					}) {
						return await new Promise((resolve, reject) => {
							if (state.openid) {
								resolve(state.openid)
							} else {
								uni.login({
									success: (data) => {
										commit('login')
										setTimeout(function() { // openid
											const openid = '123456789'
											console.log(
												'uni.request mock openid[' +
												openid + ']');
											commit('setOpenid', openid)
											resolve(openid)
										}, 1000)
									},
									fail: (err) => {
										console.log('uni.login ',
											err)
										reject(err)
									}
								})
							}
						})
					},
					getPhoneNumber: function({
						commit
					}, univerifyInfo) {
						return new Promise((resolve, reject) => {
							uni.request({
								url: 'https://97fca9f2-41f6-449f-a35e-3f135d4c3875.bspapp.com/http/univerify-login',
								method: 'POST',
								data: univerifyInfo,
								success: (res) => {
									const data = res.data
									if (data.success) {
										resolve(data.phoneNumber)
									} else {
										reject(res)
									}

								},
								fail: (err) => {
									reject(res)
								}
							})
						})
					}
				},
				getters: {
					token: state => state.token,
					username: state => {
						state.userid = uni.getStorageSync(USER_INFO).username;
						return state.username
					},
					realname: state => {
						state.userid = uni.getStorageSync(USER_INFO).realname;
						return state.user.realname
					},
					avatar: state => {
						state.userid = uni.getStorageSync(USER_INFO).avatar;
						return state.user.avatar
					},
					userid: state => {
						state.userid = uni.getStorageSync(USER_INFO).id;
						return state.userid
					},
					phone: state => {
						state.userid = uni.getStorageSync(USER_INFO).phone;
						return state.phone
					},
					// phone: state => state.phone,
					// company:state => {state.userid=uni.getStorageSync(USER_INFO).companyId; return state.companyId},
				},
				state: {
					user: {
						username: uni.getStorageSync(USER_INFO).username,
						id: uni.getStorageSync(USER_INFO).id,
						realname: uni.getStorageInfoSync(USER_INFO).realname
					},
				}
			})

			export default store